	module.exports = {"data":{"site":{"siteMetadata":{"title":"Altitude Adventure Holidays","author":"Riddho Datta"}},"markdownRemark":{"id":"/Users/nirmalya/Development/personal/github/altitude-adventure-holidays/src/pages/leave-only-footprints-behind/index.md absPath of file >>> MarkdownRemark","html":"<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/altitude-adventure-holidays/static/1-2a707a7be3d871d5b62a4cf5150df8ad-88834.jpg\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 1000px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 66.625%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Leave ‘only’ Footprints Behind\"\n        title=\"\"\n        src=\"/altitude-adventure-holidays/static/1-2a707a7be3d871d5b62a4cf5150df8ad-2b053.jpg\"\n        srcset=\"/altitude-adventure-holidays/static/1-2a707a7be3d871d5b62a4cf5150df8ad-8a2b5.jpg 250w,\n/altitude-adventure-holidays/static/1-2a707a7be3d871d5b62a4cf5150df8ad-b0cf5.jpg 500w,\n/altitude-adventure-holidays/static/1-2a707a7be3d871d5b62a4cf5150df8ad-2b053.jpg 1000w,\n/altitude-adventure-holidays/static/1-2a707a7be3d871d5b62a4cf5150df8ad-44b5b.jpg 1500w,\n/altitude-adventure-holidays/static/1-2a707a7be3d871d5b62a4cf5150df8ad-88834.jpg 1600w\"\n        sizes=\"(max-width: 1000px) 100vw, 1000px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>We live in a time when we’ve gone beyond ‘pollution’ and ‘global warming’. Through repetitive and unfruitful use, we’re truly and surely bored of our ‘diplomatic’ effort of saving the earth. We’ve thought about reducing carbon footprints, we’ve Googled about renewable energy sources, some of us even pondered at the thought of replacing his Motorcycle with an e-bike!</p>\n<p>We all wait for a better tomorrow. But there will be no better tomorrow if there is no better ‘us’. Can we change the world? No chance. But we can save the surrounding we’re in. We can easily keep it clean. We can easily switch our 4 wheeler for a bicycle, for the ride to work, everyday. We can easily save our mountains, rivers, countrysides from having the same fate of our cities.</p>\n<p>We leave the ‘big’ job of tackling issues such as Global Warming to the white collared men, whereas we, take up the ‘small’ motto of keep our mountains clean. No, we don’t have t-shirts with slogans like ‘leaving only footprints behind’, and trudging the mountain slopes with a 30-40 men and women having a party in the Himalayas. We operate strictly in small groups, which also enables us for better personalized attention, and finds us in a much better situation to tackle ‘Littering’.\nAs a really ‘small’ entity, we really can’t help the cause any more than doing our part thoroughly. But we hope that helps, somewhere. Cause we really love our mountains, and we always ensure we go that extra mile, or two, to keep it pristine.</p>","frontmatter":{"title":"Leave ‘only’ Footprints Behind","date":"July 12, 2017"}}},"pathContext":{}}

